// –ü–µ—Ä–µ–≤–∏–∫–æ—Ä–∏—Å—Ç–æ–≤—É—î–º–∏–π –∫–æ–º–ø–æ–Ω–µ–Ω—Ç, —â–æ —Ä–µ–Ω–¥–µ—Ä–∏—Ç—å —É –º–æ–¥–∞–ª—å–Ω–æ–º—É –≤—ñ–∫–Ω—ñ –ø–∏—Ç–∞–Ω–Ω—è, –æ—Ç—Ä–∏–º–∞–Ω–µ —É –≤–∏–≥–ª—è–¥—ñ childrens,
// —Ç–∞ 2 —Ñ—É–Ω–∫—Ü—ñ–æ–Ω–∞–ª—å–Ω—ñ –∫–Ω–æ–ø–∫–∏. –ú–æ–¥–∞–ª—å–Ω–µ –≤—ñ–∫–Ω–æ –ø–æ–≤–∏–Ω–Ω–æ –∑–∞–∫—Ä–∏–≤–∞—Ç–∏—Å—å –ø–æ –∫–ª—ñ–∫—É –Ω–∞ –∫–Ω–æ–ø–∫—É Cancel,
// –ø–æ –∫–ª—ñ–∫—É –Ω–∞ –∫–Ω–æ–ø–∫—É –∑–∞–∫—Ä–∏—Ç—Ç—è, –∫–ª—ñ–∫ –ø–æ –±–µ–∫–¥—Ä–æ–ø—É, –∞ —Ç–∞–∫–æ–∂ –∫–ª—ñ–∫ –ø–æ Escape.
// –ü–æ –∫–ª—ñ–∫—É –Ω–∞ Yes –Ω–µ–æ–±—Ö—ñ–¥–Ω–æ –≤–∏–∫–ª–∏–∫–∞—Ç–∏ —Ñ—É–Ω–∫—Ü—ñ—é, –ø–µ—Ä–µ–¥–∞–Ω—É –≤ ModalApproveAction —É –≤–∏–≥–ª—è–¥—ñ props –Ω–∞ –≤–∏–ø–∞–¥–æ–∫ –∑–≥–æ–¥–∏ –∫–æ—Ä–∏—Å—Ç—É–≤–∞—á–∞

import PropTypes from 'prop-types';
import { useEffect } from 'react';
import { Button, CloseIcon, Content, Modal, Overlay } from './ModalApproveAction.styled';
import icons from 'images/icons.svg';

export const ModalApproveAction = ({ children, onClose}) => {
  //   –ø—Ä–æ–ø—Å onClose —Ü–µ —Ñ—É–Ω–∫—Ü—ñ—è toggleModal,
  //   —è–∫—É –ø–æ—Ç—Ä—ñ–±–Ω–æ –ø–µ—Ä–µ–¥–∞—Ç–∏ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç—É ModalApproveAction
  //   toggleModal - –∑–º—ñ–Ω—é—î —Å—Ç–µ–π—Ç isOpen

  const onBackdropClose = event => {
    if (event.target === event.currentTarget) {
      onClose();
    }
  };

  useEffect(() => {
    const onEscapeClose = event => {
      if (event.code === 'Escape') {
        onClose();
      }
    };

    window.addEventListener('keydown', onEscapeClose);
    document.body.style.overflow= "hidden";

    return () => {
      document.body.style.overflow = "visible";
      window.removeEventListener('keydown', onEscapeClose);
    };
  }, [onClose]);

  return (
    <>
      <Overlay onClick={onBackdropClose}>
        <Modal >
          <Button type="button" apia-label="close" onClick={onClose}>
            <CloseIcon>
              <use href={icons + '#cross-small'} />
            </CloseIcon>
          </Button>
          <Content>{children}</Content>
        </Modal>
      </Overlay>
    </>
  );
};

ModalApproveAction.propTypes = {
  onClose: PropTypes.func.isRequired,
  children: PropTypes.node, 
};
