// –ö–æ–º–ø–æ–Ω–µ–Ω—Ç —Ä–µ–Ω–¥–µ—Ä–∏—Ç—å –ø–æ–ª–µ –¥–ª—è –ø–æ—à—É–∫—É –æ–≥–æ–ª–æ—à–µ–Ω–Ω—è –≤ –∫–∞—Ç–µ–≥–æ—Ä—ñ—ó –ø–æ –≤–º—ñ—Å—Ç—É –∫–ª—é—á–æ–≤–æ–≥–æ —Å–ª–æ–≤–∞ –≤ –∑–∞–≥–æ–ª–æ–≤–∫—É –æ–≥–æ–ª–æ—à–µ–Ω–Ω—è. –Ø–∫—â–æ –ø–æ–ª–µ –¥–ª—è –ø–æ—à—É–∫—É –∑–∞–ø–æ–≤–Ω–µ–Ω–µ - –ø–æ—Ä—è–¥ –∑ —ñ–∫–æ–Ω–∫–æ—é –ª—É–ø–∏ –ø–æ–≤–∏–Ω–Ω–∞ –∑ º—è–≤–ª—è—Ç–∏—Å—å –∫–Ω–æ–ø–∫–∞ –∑ —ñ–∫–æ–Ω–∫–æ—é —Ö—Ä–µ—Å—Ç–∏–∫–∞, –ø–æ –∫–ª—ñ–∫—É –Ω–∞ —è–∫—É, –ø–æ–ª–µ –¥–ª—è –ø–æ—à—É–∫—É –æ—á–∏—â—É—î—Ç—å—Å—è. –ü–æ—à—É–∫ –æ–≥–æ–ª–æ—à–µ–Ω—å –ø–æ –≤–º—ñ—Å—Ç—É –∫–ª—é—á–æ–≤–æ–≥–æ —Å–ª–æ–≤–∞ –≤ –∑–∞–≥–æ–ª–æ–≤–∫—É –æ–≥–æ–ª–æ—à–µ–Ω–Ω—è –≤—ñ–¥–±—É–≤–∞—î—Ç—å—Å—è –ø–æ —Å–∞–±–º—ñ—Ç—É –∞–±–æ –∫–ª—ñ–∫—É –Ω–∞ —ñ–∫–æ–Ω–∫—É –ª—É–ø–∏.
import { useState, useMemo, useEffect } from 'react';
import { useSearchParams } from 'react-router-dom';
import { toast } from 'react-hot-toast';
import { SearchButton, SearchInput, SearchWrapper, SearchTitle } from './NoticesSearch.styled'
import CleanInput from '../../images/icons/cross-small.svg'

const NoticesSearch = ({ onSubmit }) => {
  const [search, setSearch] = useState('')
  const [searchParams] = useSearchParams();
  const params = useMemo(
    () => Object.fromEntries([...searchParams]),
    [searchParams]
  );

  useEffect(() => {
    if (params.query === undefined) {
      setSearch('');
    }
  }, [params.query]);

  const handleSearch = (e) => {
    setSearch(e.target.value)
  }

  const handleCleanInput = () => {
    setSearch('')
  }

  const handleSubmit = (e) => {
    e.preventDefault();
    if (search.trim() === '') {
      return toast.error('Enter your request, please', {
        icon: 'üòâ',
        style: {
          position: 'top-center',
          duration: 2000,
          autoclose: 1000,
          background: '#54ADFF',
          color: '#fff',
        },
      });
    }
    onSubmit(search)
    setSearch('')
  }

    return (
      <div><SearchTitle>Find your favorite pet</SearchTitle>
      <SearchWrapper>
      <SearchInput placeholder='Search' value={search} onChange={(e) => handleSearch(e)} search={search} />
      <SearchButton search={search} onClick={handleSubmit} />
      {search ? <img src={CleanInput} alt="" onClick={handleCleanInput} /> : null}
            </SearchWrapper>
            </div>
  )
}

export default NoticesSearch;
